import React from 'react';
import { motion } from 'framer-motion';

function Home() {
  return (
    <div className="Home">
      <motion.h1
        initial={{ scale: 0 }}
        animate={{ scale: 1 }}
        transition={{ duration: 1 }}
      >
        Shuxyzz
      </motion.h1>
    </div>
  );
}

export default Home;
